body {
  background: #d5d5d5; 
  font-family: arial;
  color: #FAFAFA;
}

ul {
  list-style: none;
}

li {
  margin: .65em 0;
}

h3 {
  margin-top: 0;
}

.scoreboard {
  background: #ffa31a;
  width: 800px;
  margin: 70px auto;
  box-shadow: 0 4px 0px #333;
  border-radius: 15px;
}

.header {
  padding: 5px 20px;
  text-align: center;
  display: flex;
  align-items: center;
  background-color: #222;
  border-radius: 15px 15px 0 0;
  border-bottom: solid 4px #444;
}

.header h1 {
  flex-grow: 1;
  font-size: 1.5em;
  letter-spacing: 3px;
  font-weight: bold;
  padding-left: 1em;
}

.header .stats,
.header .stopwatch {
  width: 170px;
}

.stats {
  margin-top: 0;
  font-weight: normal;
}

  .stats td:first-child {
    text-align: right;
    font-weight: normal;
    letter-spacing: 2px;
    color: #666;
    font-size: .7em;
    white-space: nowrap;
  }

  .stats td:last-child {
    text-align: left;
    white-space: nowrap;
    letter-spacing: 2px;
  }


.candidate {
  display: flex;
  font-size: 1.2em;
  border-bottom: solid 2px #444;
  letter-spacing: 2px;
  cursor: pointer;
}

  .remove-candidate {
    visibility: hidden;
    margin-right: 10px;
    color: red;
    cursor: pointer;
    background-color: transparent;
    border: none;
  }

  .candidate-name:hover .remove-candidate {
    visibility: visible;
  }

  .candidate-name {
    flex-grow: 1;
    padding: 20px 10px 20px 30px;
    font-weight: normal;
    font-size: 1.2em;
  }

  .candidate-score {
    width: 250px;
    background: blue;
  }

  .counter {
    display: flex;
  }

  .counter-action {
    border: none;
    font-weight: bold;
    color: #FAFAFA;
    display: block;
    padding: 20px 20px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .counter-action.increment {
    background: #66BB6A;
  }

  .counter-action.increment:hover {
    background: #549d59;
    cursor: pointer;
  }

  .counter-action.decrement {
    background: #ef5350;
  }

  .counter-action.decrement:hover {
    background: #c44442;
    cursor: pointer;
  }

  .counter-score {
    flex-grow: 1;
    background: #2b2b2b;
    color: #FAFAFA;
    text-align: center;
    font-family: monospace;
    padding: 10px;
    font-size: 1.8em;
    height: 1.7em;
  }


.add-candidate-form form {
  display: flex;
  background-color: #222;
}

  .add-candidate-form input[type=text] {
    flex-grow: 1;
    border-width: 0 0 1px 0;
    margin: 15px 10px 15px 15px;
    padding: 10px;
    border-radius: 5px;
    background-color: #333;
    border-style: none;
    text-shadow: none;
    text-transform: uppercase;
    color: #999;
    letter-spacing: 2px;
    outline: none;
  }

  .add-candidate-form input[type=text]::-webkit-input-placeholder{
    color: #666;
    letter-spacing: 2px;
  }

  .add-candidate-form input[type=text]:focus{
    background-color: #444;
  }

  .add-candidate-form input[type=submit] {
    display: block;
    font-size: .6em;
    margin: 15px 15px 15px 0; 
    padding: 10px;
    background-color: #333;
    border-radius: 5px;
    border: none;
    color: #999;
    letter-spacing: 2px;
    font-weight: bold;
    text-shadow: none;
    text-transform: uppercase;
  }

  .add-candidate-form input[type=submit]:hover{
    background: #4b71b5;
    color: #fafafa;
    cursor: pointer;
  }
  
.candidate-detail {
  font-size: 1.1em;
  padding: 1.65em 0 1.4em 3em;
  background: #4b71b5;
}
  .candidate-detail p {
    margin: 0;
    color: rgba(255,255,255, .75);
  }
  
  .candidate-detail ul {
    padding: 0;
    text-transform: initial;
  }
  
  .candidate-detail ul span {
    color: rgba(255, 255, 255, .43);
    display: inline-block;
    padding-right: .6em;
    width: 3.8em;
  }